*{
  margin: 0px;
  padding: 0px;
  outline: none;
}
nav{
  width: 100%;
  height: 15vh;
  background: linear-gradient(rgba(2, 2, 2, 0.925),rgba(0, 174, 255, 0.536));
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: space-evenly;
  
}
nav a{
  font-size: 18px ;
  text-decoration: none;
  color: #ffffff94;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-weight: bolder;
}
nav a:hover{
  font-size: 19px;
  color: rgb(0, 0, 0);
}
.ab{
  font-size: 18px ;
  text-decoration: none;
  color: black;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-weight: bolder;
}
.c0{
  width: 100%;
  height: 75vh;
  display: flex;
  justify-content: center;
  overflow: scroll;
  scrollbar-width: none;
}
.c1{
display: flex;
flex-direction: column;
width: 110%;
align-items: center;
justify-content: center;
background-color: rgba(0, 174, 255, 0.212);
border-radius: 4px;
border: 6px  rgba(0, 183, 250, 0.41);
border-style: groove;
padding: 10px;
margin-left: 45%;
margin-top: 45%;
}
.c2{
  display: flex;
  flex-direction: column;
  width: 70%;
  text-align: center;
  padding: 70px;
  margin-left: 10%;
  margin-top: -13px;
  
}
.tbs {
  overflow: scroll;
  scrollbar-width: none;
}
th,tr:nth-child(odd){
  background-color: rgba(0, 183, 250, 0.41);
  padding: 10px;
}
th{
  height: 10vh;
}
td{
  background-color: rgba(0, 174, 255, 0.212);
  padding: 10px;
  height: 2vh;
  
}
.c1 input{
  padding: 4px;
  margin-top: 8px;
  border: none;
  border-radius: 5px;
}
h2{
  margin-top: -30px;
  margin-bottom: 30px;
}
.c1 button{
  margin-top: 12px;
  border: none;
  background-color: rgba(0, 116, 199, 0.555);
  padding: 4px;
  border-radius: 8px;
}
.gt{
  margin: auto;
  padding: 4%;
  align-items: center;
  justify-content: center;
}
.gt td,th{
  height: 3vh;
}
.get{
  height: 75vh;
  overflow: scroll;
  scrollbar-width: none;
}
.v8{
 display: flex;
 justify-content: center;
 gap: 10px;
 margin-top: 45px;
 padding: 8px;
}
.v8 input{
  border-top: none;
  border-left: none;
  border-right: none;
  padding: 4px;
  font-size: 16px;
}
.v8 button{
  border: none;
  background-color: rgba(0, 116, 199, 0.555);
  padding: 8px;
  border-radius: 8px;
}
.r1{
  text-align: center;
  padding: 10px;
  color: red;
}
