  border-radius: 5px;
  font-size: 15px;

}
.b2 button{
  border: 2px solid;
  border-color:rgba(0, 174, 255, 0.656);
  background-color: white;
  margin-top: 15px;
  padding: 10px;
  border-radius: 7cap;
